//
// Lists
//
// stylelint-disable
ul,
ol {
  &.bulletless {
    @include bulletless();
  }
  &.inline {
    @include bulletless();

    li {
      display: inline-block;

      &:not(:last-child) {
        margin-right: $list-inline-padding;
      }
    }
  }
}
// stylelint-enable

// Inline turns list items into inline-block

//
// Misc
//

// Builds on `abbr`
.initialism {
  --#{$prefix}font-size: #{$initialism-font-size};
  --#{$prefix}line-height: #{$initialism-line-height};
  @include font-size();
  @include line-height();
  text-transform: uppercase;
}

// Blockquotes
.blockquote {
  @include map-font($blockquote-font);

  margin-bottom: $blockquote-margin-y;
  @include font();

  > :last-child {
    margin-bottom: 0;
  }
}

.blockquote-footer {
  @include map-font($blockquote-footer-font);
  margin-top: -$blockquote-margin-y;
  margin-bottom: $blockquote-margin-y;
  @include font();
  color: $blockquote-footer-color;

  &::before {
    content: "\2014\00A0"; // em dash, nbsp
  }
}

// Font sizes with line height
//
@each $name, $value in $font-sizes {
  .font-#{$name} {
    //stylelint-disable-next-line property-disallowed-list
    font-size: var(--#{$prefix}font-size-#{$name}) if($enable-important-utilities, !important, null);
    line-height: var(--#{$prefix}line-height-#{$name}) if($enable-important-utilities, !important, null);
  }
}

.font-smaller {
  //stylelint-disable-next-line property-disallowed-list
  font-size: $font-size-smaller if($enable-important-utilities, !important, null);
  line-height: $line-height-smaller if($enable-important-utilities, !important, null);
}
.font-larger {
  //stylelint-disable-next-line property-disallowed-list
  font-size: $font-size-larger if($enable-important-utilities, !important, null);
  line-height: $line-height-larger if($enable-important-utilities, !important, null);
}

.font-monospace {
  @extend .font-code;
}

// Type display classes
@each $name, $value in $display-font-sizes {
  .font-display.font-#{$name} {
    //stylelint-disable-next-line property-disallowed-list
    font-size: var(--#{$prefix}font-size-display-#{$name}) if($enable-important-utilities, !important, null);
    line-height: var(--#{$prefix}line-height-display-#{$name}) if($enable-important-utilities, !important, null);
  }
}

// Type display classes
@each $name, $value in $display-font-weights {
  .font-display.font-#{$name} {
    font-weight: var(--#{$prefix}font-weight-display-#{$name}) if($enable-important-utilities, !important, null);
  }
}

// Type display classes
@each $name, $value in $context-fonts {
  .font-#{$name} {
    $font-family: map-get($value, "font-family");
    $font-size: map-get($value, "font-size");
    $line-height: map-get($value, "line-height");
    $font-weight: map-get($value, "font-weight");
    $font-style: map-get($value, "font-style");
    $letter-spacing: map-get($value, "letter-spacing");
    $text-transform: map-get($value, "text-transform");
    $text-decoration: map-get($value, "text-decoration");
    $margin-bottom: map-get($value, "margin-bottom");
    margin-bottom: $margin-bottom;
    font: $font-style $font-weight #{$font-size} / #{$line-height} $font-family;
    text-decoration: $text-decoration;
    text-transform: $text-transform;
    letter-spacing: $letter-spacing;
    // @include map-font($context-fonts, $prop: true);
  }
}


