%font {
  font: var(--#{$prefix}font-weight) var(--#{$prefix}font-size) / var(--#{$prefix}line-height) var(--#{$prefix}font-family) if($enable-important-utilities, !important, null);
}

%context-font {
  margin-bottom: var(--#{$prefix}paragraph-spacing) if($enable-important-utilities, !important, null);
  font: var(--#{$prefix}font-weight) var(--#{$prefix}font-size) / var(--#{$prefix}line-height) var(--#{$prefix}font-family) if($enable-important-utilities, !important, null);
  text-decoration: var(--#{$prefix}text-decoration) if($enable-important-utilities, !important, null);
  text-transform: var(--#{$prefix}text-transform) if($enable-important-utilities, !important, null);
  letter-spacing: var(--#{$prefix}letter-spacing) if($enable-important-utilities, !important, null);
  // @include map-font($context-fonts, $prop: true);

  > h1,
  > h2,
  > h3,
  > h4,
  > h5,
  > h6,
  > p {
    margin-bottom: var(--#{$prefix}paragraph-spacing) if($enable-important-utilities, !important, null);
  }
}

%font-size {
  //stylelint-disable-next-line property-disallowed-list
  font-size: var(--#{$prefix}font-size) if($enable-important-utilities, !important, null);
  line-height: var(--#{$prefix}line-height) if($enable-important-utilities, !important, null);
}

%fg-color {
  color: var(--#{$prefix}fg-color) if($enable-important-utilities, !important, null);
}

%bg-color {
  background-color: var(--#{$prefix}bg-color) if($enable-important-utilities, !important, null);
}

%border-color {
  border-color: var(--#{$prefix}border-color) if($enable-important-utilities, !important, null);
}

%icon-color {
  color: var(--#{$prefix}icon-color) if($enable-important-utilities, !important, null);
  fill: var(--#{$prefix}icon-color) if($enable-important-utilities, !important, null);
}
%link-color {
  color: RGBA(var(--#{$prefix}link-color-rgb), var(--#{$prefix}link-opacity, 1)) if($enable-important-utilities, !important, null);
  text-decoration-color: RGBA(var(--#{$prefix}link-color-rgb), var(--#{$prefix}underline-opacity, 1)) if($enable-important-utilities, !important, null);
}
